@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;700&display=swap');

body {
	font-family: 'Montserrat', sans-serif;
	background: black;
	color: white;
	font-weight: 400;
	letter-spacing: 0.05em;
}

main {
	margin: auto;
	max-width: 800px;
}

div[class^='container-'] {
	border-top: 2px dotted #a04caf;
	padding-top: 0px;
	margin: 50px auto;
}

footer {
	margin-top: 100px;
	display: flex;
	justify-content: space-between;
}

footer a {
	cursor: pointer;
	color: royalblue;
	text-decoration: none;
}
footer a:hover {
	color: #a04caf;
}
